<template>
    <view class="things_user_info">
		<!-- tabbar -->
        <cu-custom :refresh="preRrefresh"><block slot="content">个人信息</block></cu-custom>
        <form class="things_user_info_form" @submit="formSubmit">
            <view class="cu-form-group">
				<view class="title">我的ID</view>
				<input class="valueInput" placeholder="我的ID" name="user_id" :disabled="true" :value="userInfo.id" />
			</view>
            <view class="cu-form-group">
				<view class="title">口碑</view>
				<input class="valueInput" placeholder="口碑" name="reputation" :disabled="true" :value="userInfo.reputation" />
			</view>
            <view class="cu-form-group">
				<view class="title">认证</view>
				<input class="valueInput" placeholder="认证" name="type" :disabled="true" :value="userInfo.type | identity_format" />
			</view>

            <view class="cu-form-group">
				<view class="title">姓名</view>
				<input class="valueInput" placeholder="请输入姓名" name="real_name" :value="userInfo.realName"  />
			</view>
            <view class="cu-form-group">
				<view class="title">手机号码</view>
				<input class="valueInput" placeholder="请输入手机号码" name="mobile" :value="userInfo.mobile" />
			</view>
            <view class="cu-form-group">
				<view class="title">身份证</view>
				<input class="valueInput" placeholder="请输入身份证" name="identity_card" :value="userInfo.identityCard" />
			</view>
            <view class="cu-form-group">
				<view class="title">微信号</view>
				<input class="valueInput" placeholder="请输入微信号" name="weixin_code" :value="userInfo.weixinCode" />
			</view>

            <view class="cu-form-group">
				<view class="title">支付宝账号</view>
				<input class="valueInput" placeholder="请输入支付宝账号" name="apay_code" :value="userInfo.apayCode" />
			</view>
            <view class="cu-form-group">
				<view class="title">持卡人姓名</view>
				<input class="valueInput" placeholder="请输入持卡人姓名" name="card_holder_name" :value="userInfo.cardHolderName" />
			</view>
            <view class="cu-form-group">
				<view class="title">开户行</view>
				<input class="valueInput" placeholder="请输入开户行" name="bank_addr" :value="userInfo.bankCode" />
			</view>
            <view class="cu-form-group">
				<view class="title">所在城市</view>
				<input class="valueInput" placeholder="请选择城市" name="region" :value="userInfo.cityId" :disabled="true" @tap="openWPicker" />
			</view>
            <!-- 保存 -->
            <view class="flex justify-center align-center save_btn_box">
                <button form-type="submit" class="cu-btn saveBtn">保存</button>
            </view>
        </form>
		<!-- 城市三级联动框 -->
		<w-picker 
			:visible.sync="wPicker.visible"
			mode="region"
			:value="wPicker.defaultRegion"
			default-type="value"
			:hide-area="false"
			@confirm="selectAddress($event,'region')"
			@cancel="cancelAddress"
			themeColor="#32d08b"
			ref="region">
		</w-picker>
		<!-- loading弹框组件 -->
		<popLinkDialog></popLinkDialog>
		<popLoading></popLoading>
		<popDialog></popDialog>
		<tipModal></tipModal>
		<!-- 登录 -->
		<login></login>
    </view>
</template>
<script>
	import { getPersonalInfo_api,fillInPersonalInfo_api } from '@/api/index.js';
	import { appData } from '@/utils/config.js';
	import { mapState, mapGetters, mapActions, mapMutations } from 'vuex';
	import storage from '@/utils/storage.js';
	import { identity_format } from '@/utils/filters.js';
	let that = null;
	export default {
		data() {
			return {
				userInfo: {},
				wPicker: {
					visible: false,
					defaultRegion:["150000","150100","150103"]
				},
				preRrefresh: false,
			}
		},
		watch: {
			'$store.getters.user_token': function(val) {
				// token 发生变化，重新请求数据
				that.getUserInfo();
			}
		},
		// 过滤器
		filters:{
			// 认证
			identity_format
		},
		methods: {
			...mapMutations([
				'modal',
				'user'
			]),
			...mapActions([
				'demoactions'
			]),
			/**打开地区选择器
			* @description: 
			* @param {*} e
			* @return {*}
			* @author: gitxinghui
			*/
			openWPicker(e) {
				that.wPicker.visible = true;
			},
			/**地区确定
			* @description: 
			* @param {*} e
			* @return {*}
			* @author: gitxinghui
			*/
			selectAddress(e) {
				console.log(e);
			},
			/**地区取消
			* @description: 
			* @param {*} e
			* @return {*}
			* @author: gitxinghui
			*/
			cancelAddress(e) {

			},
			/**获取用户信息
			* @description:
			* @param {*} e
			* @return {*}
			* @author: gitxinghui
			*/
			getUserInfo(e) {
				getPersonalInfo_api({},that).then(res=>{
					console.log(res);
					that.userInfo = res.data.userInfo;
				}).catch(err=>{
					console.log(err);
				})
			},
			/**填写个人信息保存
			* @description: 
			* @param {*} e
			* @return {*}
			* @author: gitxinghui
			*/
			formSubmit(e) {
				that.modal({name:'popLoading',value:true});
				fillInPersonalInfo_api(e.detail.value,that).then(res=>{
					that.modal({name:'popLoading',value:false});
					that.modal({name:'popTipModal',value:{type:true,tipTitle:'修改',tipContent:'修改成功'}});
					that.getUserInfo();
				}).catch(err=>{
					console.log(err);
					that.modal({name:'popLoading',value:false});
					that.modal({name:'modal_popDialog',value:{type:true,tipTitle:'错误',tipContent:err}});
				})
			}
		},
		onLoad(options) {
			that = this;
			that.getUserInfo();
		}
	}
</script>
<style lang="scss" scoped>
    @import './index';
</style>
